@import url(https://fonts.googleapis.com/css?family=Merriweather:300,400|Oswald:200&display=swap);
html, body {
  display: flex;
  flex-flow: column;
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(80%, #29323c), to(#485563));
  background-image: linear-gradient(#29323c 80%, #485563);
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-navbar {
  flex-direction: row;
  font-size: 1.5rem;
}

.App-dropdownMenu {
  background-color: #29323c;
  border-color: 	#485563;
  font-size: 1.25rem;
}

.App-navLinks {
  text-align: center;
  color: #EDE5CE;
}

.navBrand {
   padding: 10px;
}
.App-navLinks:hover {
  background-image: radial-gradient(#29323c 80%, #485563);
  color: #EDE5CE;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

.main{
  margin: 1%;
  height: 85vh;
}

.oneDriveDoc {
  width: 100%;
  height: 100%;
}

.Education-body {
  color: #EDE5CE;
  font-size: 1.2rem;
  padding-top: 2%;
}

.educationMainBox {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}

.logo {
  text-align: center;
  width: 100%;
  margin-bottom: 3%;
}

.educationBox {
  flex-direction: column;
  width: 45%;
  min-width: 300px;
  margin: auto;
  padding: 1%;
}
.careerMainBox {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.careerLogos {
  display: flex;
  flex-direction: row;
  margin: 1% auto auto auto;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  align-items: center;
}

.border {
  border-style: solid;
}

.logoLink {
  min-width: 300px;
  margin: 1% 0% 1% 0%;
  width: 30%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.logoLink:hover {
  background-image: radial-gradient(#29323c 80%, #485563);
}

.logo {
  width: 100%;
  margin: 0;
}

.careerBody {
  color: #EDE5CE;
  height: 100%;
}

.careerJobTitle {
  font-size: 1.7rem;
