  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.clickableCareerJobTitle {
  font-size: 1.7rem;
  cursor: pointer;
  font-family: 'Merriweather', sans-serif;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.clickableCareerJobTitle:hover {
  background-image: radial-gradient(#29323c 80%, #485563);
}

.careerJobResponsibilities {
